body{
    width: 100%;
}
.coin-name{
    padding: 20px 40px;
    padding-top:90px;
     color:black;
}
.card-sec{
    display: flex;
    gap:20px;
    margin:auto;
    padding:20px 40px;
    width:fit-content;
    /* position:sticky; */
}
.cb{
    width: 12rem; 
    
}
.ctxt{
    text-align:center; 
    color:#e54230;
     font-size:1.1rem;
     font-weight:600;
}
.ctxt1{
        text-align:center; 
        color:#31c002;
         font-size:1.1rem;
         font-weight:600;
    }

.ctxt2{
    text-align:center; 
    color:#910000;
     font-size:1.1rem;
     font-weight:600;
}
.ct{
    text-align: center ;
     font-size:1rem;
      font-weight:600;
       color:black;
}
.current-price{
    background-Color:transparent;
    width: 100%;
    
}
.current-price p{
    text-align:center; 
    color:black;
     font-size:1.15rem;
}
.current-price h1{
    text-align:center; 
    font-size:4rem;
     color:#e54230;
    font-weight:700;
}
@media screen and (min-width: 768px) and (max-width: 1023px){
 
    .coin-name{
        padding: 40px 40px;
    }
    .card-sec{
        width: 100%;
        gap: 10px;
        display:inline-flex;
    }

    .current-price{
        width: 100%;
       
        height: fit-content;
        background-size: cover;
    }
    .current-price p{
        text-align: center;
        width: fit-content;
        margin: auto;
    }
    .current-price h1{
        text-align: center;
        width: fit-content;
        margin: auto;
    }
}
@media screen and (max-width:767px){
    .coin-name{
        padding: 30px 40px;
    }
    .card-sec{
        width: 100%;
        gap: 20px;
        display: grid;
        justify-content: center;
        
    }
    .cb{
        width: 20rem;

    }

    .current-price{
        width: 100%;
       
        height: fit-content;
        background-size: cover;
    }
    .current-price p{
        text-align: center;
        width: fit-content;
        margin: auto;
        font-size: 1rem;
    }
    .current-price h1{
        text-align: center;
        width: fit-content;
        margin: auto;
        font-size: 3.2rem;
    }
}