.main-chart-div{
    display:flex;
    gap:20px;
    width:100%;
}
.chart1{
    max-width: 550px;
}
.area{
    padding:40px 40px;
    max-width:fit-content;
}
.second-chart{
    display:flex;
     gap:30px;
     margin-top:123px;
     margin-left:55px;
}
.chart2{
    max-width:350px;
     float:right;
}
.line1{
    padding:40px 40px;
    max-width:fit-content;
}
.chart3{
    max-width:350px;
     float:right;
}
.line2{
    padding:40px 40px;
    max-width:fit-content;
}
.main-foot{
    width:55%;
    display:flex;
    margin:auto;
}
.inner-main{
    display:flex;
    margin:auto;
    width:90%;
}
.f-con{
    width:35%;
    height:fit-content;
    text-align:center;
}
.s-con{
    width:35%;
    height:fit-content;
    text-align:center;
}
.t-con{
    width:35%;
    height:fit-content;
    text-align:center;
}
.fo-con{
    width:35%;
    height:fit-content;
    text-align:center;
}
.h2{
    font-size:1.1rem;
}
.p{
    color:#e54230;
    font-size:1.05rem;
}
@media screen and (min-width: 768px ) and (max-width: 1023px){
    .main-chart-div{
        display: grid;
        gap: 20px;   
    }
    .chart1{
        margin: auto;
        justify-content: center;
    }
   
    .area{
        padding: 50px 0px;
    }
    .second-chart {
        display: flex;
        gap: 10px;
        margin: auto;
    }
    .chart2{
        width: fit-content ;
        
    }
    .chart3{
        width: fit-content;
    }
    .line1{
        padding: 0px 0px;
    }
    .line2{
        padding: 0px 0px;
    }

    .main-foot{
        width:100%;
        display:flex;
        margin:auto;
        margin-top: 40px;
    }

}
@media screen and (max-width: 767px){
    .main-chart-div{
        display: grid;
        gap: 20px;   
    }
    
    .chart1{
        width: 100%;
        margin: auto;
        justify-content: center;
    }
    .area{
        padding: 50px 0px;
    }
    .second-chart{
        display: grid;
        gap: 50px;
       margin: auto;
    }
    .line1{
        padding: 0px 0px;
    }
    .line2{
        padding: 0px 0px;
    }
    .main-foot{
        width:100%;
        display:flex;
        margin:auto;
        margin-top: 40px;
        
    }
    .inner-main{
        display: flex;
        margin: auto;
        gap: 10px;
        /* justify-content: center; */
        width: 100%;
    }
    .h2{
        font-size: 0.9rem;
        text-align: center;
        margin: auto;
    }
    .p{
        font-size: 1rem;
        margin: auto;
    }
}